import {
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			mpCode: ''
		}
	},
	onLoad() {
		let that = this
		// 登录

		uni.login({
			success: res => {
				if (res.code) {
					console.log("code:", res)
					this.mpCode = res.code
				} else {
					console.log('登录失败！' + res.errMsg)
				}
			}
		})

	},
	methods: {
		...mapMutations(['login']),















		

		userInfo(res) {
			let _this = this;
			let loginParam = {
				code: this.mpCode,
				platform: 'mp',
				/* encryptedData: res.detail.encryptedData, */
				encryptedData: '13227355241',
				/* iv: res.detail.iv, */
				deviceType: 1,
				nickName: res.detail.userInfo.nickName,
				avatarurl: res.detail.userInfo.avatarUrl
			}
			this.$api.request(loginParam, 'api/user/wechat-app-login', 'POST').then(resp => {
				//存储用户信息
				let userInfo = {
					avatarUrl: resp.data.data.avatarurl,
					nickName: resp.data.data.nickName
				}
				//将获取的token信息存储到缓存
				uni.setStorage({
					key: 'token',
					data: resp.data.data.token
				})
				_this.login(userInfo);
				uni.navigateBack();
			})
		}

	}
}
